<template>
    <div>
        <table class="table table-bordered w-50">
            <tr>
                <th>번호</th>
                <td>{{item.qna_no}}</td>
            </tr>
            <tr>
                <th>글쓴이</th>
                <td>{{item.qna_userid}}</td>
            </tr>
            <tr>
                <th>제목</th>
                <td>{{item.qna_title}}</td>
            </tr>
            <tr>
                <th>날짜</th>
                <td>{{item.qna_datetime}}</td>
            </tr>
            <tr>
                <td colspan="2">
                    {{item.qna_content}}
                </td>
            </tr>
            <tr v-if="item.reply_userid != null">
                <hr>
                <td>{{item.reply_userid}}</td>
                <td>{{item.reply_datetime}}</td>
                <td colspan="2">
                    {{item.reply_content}}
                </td>
            </tr>
        </table>
        <br />
        <div class="text-center">
            <router-link :to="{name: 'qnalist'}" class="btn btn-primary">목록</router-link>
            <router-link v-if="curUser == item.qna_userid" :to="{name: 'qnaupdate', params: {no: item.qna_no}}" class="btn btn-primary">수정</router-link>
            <router-link v-if="curUser == item.qna_userid" :to="{name: 'qnadelete', params: {no: item.qna_no}}" class="btn btn-primary">삭제</router-link>
            <!-- 관리자라면 reply 하는 기능 -->
            <router-link v-if="curUser == 'admin'" :to="{name: 'replycreate', params: {no: item.qna_no}}" class="btn btn-primary">답글달기</router-link>
        </div>
    </div>
</template>

<script>
    import http from '@/util/http-common.js';
    import axios from 'axios';

    export default {
        name: 'Read',
        data: function () {
            return {
                item: {},
                curUser: ''
            };
        },
        created() {
            http.get(`/${this.$route.params.no}`).then(({ data }) => {
                this.item = data;
            });

            axios.get('http://localhost:9000/happyhouse/userinfo').then(({data}) => {
                if(data.login == 'yes'){
                    this.curUser = data.curUser;
                }
            });

        }
    }
</script>

<style lang="scss" scoped>

</style>