<template>
    <div>
        <div class="form-group">
            <label for="writer">작성자</label>
            <input type="text" class="form-control" id="userid" ref="userid" placeholder="작성자를 입력하세요" v-model="item.qna_no" disabled>
        </div>
        <div class="form-group">
            <label for="title">제목</label>
            <input type="text" class="form-control" id="title" ref="title" placeholder="제목을 입력하세요" v-model="item.qna_title" disabled>
        </div>
        <div class="form-group">
            <label for="content">내용</label>
            <textarea type="text" class="form-control" id="contnet" ref="content" placeholder="내용을 입력하세요" disabled
                v-model="item.qna_content"></textarea>
        </div>
        <hr>
        <div class="form-group">
            <label for="curUser">관리자</label>
            <textarea type="text" class="form-control" id="curUser" ref="curUser" v-model="curUser"></textarea>
        </div>
        <div class="form-group">
            <label for="reply_content">답변</label>
            <textarea type="text" class="form-control" id="reply_content" ref="reply_content" placeholder="내용을 입력하세요"
                v-model="reply_content"></textarea>
        </div>
        <div class="text-right">
            <button class="btn btn-primary" @click="checkHandler">답변 등록</button>
            <button class="btn btn-primary" @click="moveList">목록</button>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    import http from '@/util/http-common.js';
    export default {
        name: 'Create',
        data: function () {
            return {
                item: {},
                curUser: '',
                reply_datetime: '',
                reply_content: '',
            };
        },
        created() {
            http.get(`/${this.$route.params.no}`).then(({ data }) => {
                this.item = data;
            });

            axios.get('http://localhost:9000/happyhouse/userinfo').then(({data}) => {
                console.log(data);
                if(data.login == 'yes'){
                    this.curUser = data.curUser;
                }
            });

        },
        methods: {
            checkHandler() {
            let err = true;
            let msg = '';
            !this.curUser && ((msg = '작성자를 입력해주세요'), (err = false), this.$refs.curUser.focus());
            err &&
                !this.reply_content &&
                ((msg = '내용 입력해주세요'), (err = false), this.$refs.reply_content.focus());

            if (!err) alert(msg);
            else this.createHandler();
            },
            createHandler() {
            http
                .put(`${this.$route.params.no}`, {
                    qna_userid: this.item.qna_userid,
                    qna_title: this.item.qna_title,
                    qna_content: this.item.qna_content,
                    reply_userid: this.curUser,
                    reply_content: this.reply_content
                })
                .then(({ data }) => {
                    let msg = '답변 등록 중 문제가 발생했습니다.';
                    if (data === 'success') {
                        msg = '답변 등록이 완료되었습니다.';
                    }
                    alert(msg);

                    this.$router.push({name:'qnadetail', params:{no: this.item.qna_no}});
                });
            },
            moveList() {
                this.$router.push({name:'qnalist'});
            },
        }
    }
</script>

<style lang="scss" scoped>

</style>